import { darken } from 'polished'
import React, { ReactNode, useCallback, useContext, useState } from 'react'
import { ChevronDown, ChevronUp } from 'react-feather'
import styled, { ThemeContext } from 'styled-components'
import { ButtonSecondary } from '../Button'
import { TYPE } from '../../theme'
import Card from '../Card'
import { AutoColumn } from '../Column'
import Row, { RowBetween, RowFixed } from '../Row'
import { MyTokenlist } from '../../state/conditionOfOrders/hooks'
import QuestionHelper from '../QuestionHelper'
import Copy from '../AccountDetails/Copy'
import ListLogo from '../ListLogo'
import { getFait } from '../../hooks/fait'
import useWrapCallback1 from '../../hooks/useWrapCallback1'
import { ethers } from "ethers";
import { useTranslation } from 'react-i18next'
import { getInfoType, INFOTYPE } from '../../hooks/describeInfoType'
import InfoTypeLOGO from '../InfoTypeLogo'
import { getJudge } from '../../hooks/judge'
import Modal from '../Modal'
import { ConfirmationPendingContent, TransactionErrorContent, TransactionSubmittedContent } from '../TransactionConfirmationModal'
import { useActiveWeb3React } from '../../hooks'

export const FixedHeightRow = styled(RowBetween)`
  height: 24px;
`
export const CryptoInput = styled.textarea`
position: relative;
display: flex;
padding: 16px;
align-items: center;
width: 100%;
white-space: nowrap;
background: none;
border: 1px solid;
outline: none;
border-radius: 12px;
text-align: left
-webkit-appearance: none;
font-size: 14px;

`
export const HoverCard = styled(Card)`
  
  border: 1px solid ${({ theme }) => theme.bg2};
  :hover {
    border: 1px solid ${({ theme }) => darken(0.06, theme.bg2)};
  }
`
const A = styled.a`
text-Decoration: none;
:hover {
  text-Decoration:underline;
}
`


function Crypto(props: { info: React.Key | undefined, handleHashComfirm: any }) {
  const theme = useContext(ThemeContext)
  const [cryptoInfo, setcryptoInfo] = useState("")
  const handleInputCryproInfo = (event: any) => {
    setcryptoInfo(event.target.value)
    if (props.info == ethers.utils.id(event.target.value)) {
      props.handleHashComfirm(3)
    } else {
      props.handleHashComfirm(2)
    }
  }
  const { t } = useTranslation()
  return (
    <div>
      <FixedHeightRow key={props.info}>
        <RowBetween>
          <RowFixed>
            <TYPE.black fontSize={14} fontWeight={400} color={theme.text2}>
              {t('Please Input Crypto Contract Item')}
            </TYPE.black>
            <QuestionHelper text={t("This order include a crypto contract item.please contact this seller to get this crypto contract item")} />
          </RowFixed>
        </RowBetween>
      </FixedHeightRow>
      <CryptoInput value={cryptoInfo} onClick={async () => {
        navigator?.clipboard?.readText()?.then(text => {
          setcryptoInfo(text);
        })
      }}
        onChange={handleInputCryproInfo}
      />
    </div>
  )
}

export default function FullPositionCard(props: any, border: any) {
  const theme = useContext(ThemeContext)
  const [showMore, setShowMore] = useState(false)
  const [contactInfo, setcontactInfo] = useState("")
  const mtoken = MyTokenlist(props.pair.erc20address);
  const generateTotalAmount = (() => {
    return (<TYPE.black fontSize={14} color={theme.text1}>{ethers.utils.formatUnits(props.pair.price.mul(props.pair.salenumber).toString(), (mtoken ? mtoken.decimals : 0) + 6) + " " + props.pair.Currency}</TYPE.black >)

  })
  function infoDescribe(infotype: string, info: string, i: number, infodescribe: string, tempINFOTYPE?: INFOTYPE): ReactNode | undefined {

    return (

      <FixedHeightRow key={i}>
        <RowBetween>
          <RowFixed>

            <InfoTypeLOGO currency={tempINFOTYPE} size={'12px'} />
            <TYPE.black fontSize={14} fontWeight={400} color={theme.text2}>
              {t(infotype) + " "}:
            </TYPE.black>
            {infodescribe ? <QuestionHelper text={t(infodescribe)} /> : null}
          </RowFixed>
          {infotype === "Telegram" ? (
            <TYPE.black fontSize={14} color={theme.text1}>
              {info && (
                <a href={"https://t.me/" + info} style={{ color: theme.text1 }} target="_blank">{info}</a>
              )} </TYPE.black>) : infotype === "QQ" ? (
                <TYPE.black fontSize={14} color={theme.text1}>
                  {info && (
                    <a href={"https://wpa.qq.com/msgrd?v=3&uin=" + info + "&site=qq&menu=yes"} style={{ color: theme.text1 }} target="_blank">{info}</a>
                  )} </TYPE.black>)
            : (<TYPE.black fontSize={14} color={theme.text1}>
              {info && (

                <span style={{ marginLeft: '3px' }}> {t('Show after locked')}</span>

              )} </TYPE.black>)}

        </RowBetween>
      </FixedHeightRow>
    )
  }

  const [hashComfirm, sethashComfirm] = useState(0)
  function handleHashComfirm(a: number) {
    sethashComfirm(a);
  }

  function HandleDescribe() {

    let aa;

    aa = Object.values(JSON.parse(props.pair.describe));

    let num = 0
    return (
      aa?.map((item, i) => {
        let item1: string = typeof (item) == "string" ? item : ""
        let infotype: string = item1?.split(':')[0];
        const tempINFOTYPE = getInfoType(infotype);
        let infodescribe: string | undefined = getInfoType(infotype)?.Describe;
        let info: string = item1?.split(':')[1];
        if (num < 1 && (infotype == "CryptoContract")) {
          num = num + 1
          if (hashComfirm == 0) {
            sethashComfirm(1)
          }
          return <Crypto info={info} key={info} handleHashComfirm={handleHashComfirm}></Crypto>
        }
        return (
          infoDescribe(infotype?infotype:"", info?info:"", i, infodescribe ? infodescribe : "", tempINFOTYPE)
        )
      })
    )
  }
  function handleLockButton() {
    if (hashComfirm == 1) {
      return (
        <ButtonSecondary width="100%" disabled={true}>
          {t('Please input Crypto contract')}
        </ButtonSecondary>)
    } if (hashComfirm == 2) {
      return (
        <ButtonSecondary width="100%" disabled={true}>
          {t('Please input correct crypto contract')}
        </ButtonSecondary>)
    } else {
      return (
        <ButtonSecondary width="100%" onClick={lock}>
          {t('Lock')}
        </ButtonSecondary>)
    }
  }
  const { t } = useTranslation()
  const ellipsisPriceAndNumber = (a: string) => {
    if (a.length > 10) {
      return a.substring(0, 10) + "..."
    }
    return a
  }
  const [{ showConfirm, swapErrorMessage, attemptingTxn, txHash }, setSwapState] = useState<{
    showConfirm: boolean
    attemptingTxn: boolean
    swapErrorMessage: string | undefined
    txHash: string | undefined
  }>({
    showConfirm: false,
    attemptingTxn: false,
    swapErrorMessage: undefined,
    txHash: undefined
  })

  const { execute: onWrap } = useWrapCallback1(props.pair.id, contactInfo, props.pair.buyerLiquidataedDamages)
  function lock() {
    if (hashComfirm == 0 || hashComfirm == 3) {

      if (onWrap) {
        setSwapState({ attemptingTxn: true, showConfirm: true, swapErrorMessage: undefined, txHash: undefined })
        onWrap().then(hash => {
          setSwapState({ attemptingTxn: false, showConfirm: true, swapErrorMessage: undefined, txHash: hash })
        }).catch(error => {
          setSwapState({
            attemptingTxn: false,
            showConfirm: true,
            swapErrorMessage: error.message,
            txHash: undefined
          })

        })
      }
    }
  }

  const { chainId } = useActiveWeb3React()
  const onDismiss = () => {
    setSwapState({ showConfirm: false, attemptingTxn, swapErrorMessage, txHash })
  }

  let pendingText: string = `${t('Locking Order')} ${props.pair.id.toString()}`

  const confirmationContent = useCallback(
    () =>
      swapErrorMessage ? (
        <TransactionErrorContent onDismiss={onDismiss} message={swapErrorMessage} />
      ) : null
    , [onDismiss, swapErrorMessage]
  )
  return (
    <>
      {showConfirm ? <Modal isOpen={showConfirm} onDismiss={onDismiss} maxHeight={90}>
        {attemptingTxn ? (
          <ConfirmationPendingContent onDismiss={onDismiss} pendingText={pendingText} />
        ) : txHash ? (
          <TransactionSubmittedContent chainId={chainId} hash={txHash} onDismiss={onDismiss} />
        ) : (
          confirmationContent()
        )}
      </Modal> : null
      }
      <HoverCard border={border}>
        <AutoColumn gap="12px">
          <FixedHeightRow onClick={() => setShowMore(!showMore)} style={{ cursor: 'pointer' }}>
            <RowFixed>
              <Row>
                {mtoken ? (
                  <ListLogo
                    style={{ marginRight: 12 }}
                    logoURI={mtoken.logoURI ? mtoken.logoURI : ""}
                    alt={`${mtoken.name} list logo`}
                  />
                ) : null
                }
                <TYPE.main id="ERC20">{mtoken?.name}</TYPE.main>
              </Row>
            </RowFixed>
            <RowFixed>
              <Row>
                <TYPE.main id="num">{ellipsisPriceAndNumber(ethers.utils.formatUnits(props.pair.salenumber, mtoken?.decimals))}</TYPE.main>
              </Row>
            </RowFixed>
            <RowFixed>
              <Row>
                <TYPE.main id="price">{ellipsisPriceAndNumber(ethers.utils.formatUnits(props.pair.price, 6))}{getFait(props.pair.Currency)?.sign}</TYPE.main>
              </Row>
            </RowFixed>
            <RowFixed>
              {showMore ? (
                <ChevronUp size="20" style={{ marginLeft: '10px' }} />
              ) : (
                <ChevronDown size="20" style={{ marginLeft: '10px' }} />
              )}
            </RowFixed>
          </FixedHeightRow>
          {showMore && (
            <AutoColumn gap="8px">

              <FixedHeightRow>
                <RowBetween>
                  <RowFixed>
                    <TYPE.black fontSize={14} fontWeight={400} color={theme.text2}>
                      {t('Order ID')}
                    </TYPE.black>
                    <QuestionHelper text={t("Each order has a unique ID.")} />
                  </RowFixed>
                  <TYPE.black fontSize={14} color={theme.text1}>
                    {props.pair.id.toString()}
                  </TYPE.black>
                </RowBetween>
              </FixedHeightRow>

              <FixedHeightRow>
                <RowBetween>
                  <RowFixed>
                    <TYPE.black fontSize={14} fontWeight={400} color={theme.text2}>
                      {t('Price')}
                    </TYPE.black>
                  </RowFixed>
                  <TYPE.black fontSize={14} color={theme.text1}>
                    {ethers.utils.formatUnits(props.pair.price, 6) + " " + props.pair.Currency}
                  </TYPE.black>
                </RowBetween>
              </FixedHeightRow>

              <FixedHeightRow>
                <RowBetween>
                  <RowFixed>
                    <TYPE.black fontSize={14} fontWeight={400} color={theme.text2}>
                      {t('Sale Number')}
                    </TYPE.black>
                  </RowFixed>
                  <TYPE.black fontSize={14} color={theme.text1}>
                    {ethers.utils.formatUnits(props.pair.salenumber, mtoken ? mtoken.decimals : 0) + " " + mtoken?.name}
                  </TYPE.black>
                </RowBetween>
              </FixedHeightRow>

              <FixedHeightRow>
                <RowBetween>
                  <RowFixed>
                    <TYPE.black fontSize={14} fontWeight={400} color={theme.text2}>
                      {t('Total Amount')}
                    </TYPE.black>
                    <QuestionHelper text={t("Total Amount = Price × saleNumber")} />
                  </RowFixed>
                  {generateTotalAmount()}
                </RowBetween>
              </FixedHeightRow>

              <FixedHeightRow>
                <RowBetween>
                  <RowFixed>
                    <TYPE.black fontSize={14} fontWeight={400} color={theme.text2}>
                      {t('Seller Address')}
                    </TYPE.black>

                  </RowFixed>
                  <TYPE.black fontSize={14} color={theme.text1}>
                    {props.pair.seller.toString() && (
                      <Copy toCopy={props.pair.seller.toString()}>
                        <span style={{ marginLeft: '4px' }}> {props.pair.seller.toString().substring(0, 6) + "..." + props.pair.seller.toString().substring(38)}</span>
                      </Copy>
                    )} </TYPE.black>

                </RowBetween>
              </FixedHeightRow>

              <FixedHeightRow>
                <RowBetween>
                  <RowFixed>
                    <TYPE.black fontSize={14} fontWeight={400} color={theme.text2}>
                      {t("Seller's liquidated damage")}
                    </TYPE.black>
                  </RowFixed>
                  <TYPE.black fontSize={14} color={theme.text1}>
                    {ethers.utils.formatEther(props.pair.sellerLiquidataedDamages)} HYK
                  </TYPE.black>
                </RowBetween>
              </FixedHeightRow>

              <FixedHeightRow>
                <RowBetween>
                  <RowFixed>
                    <TYPE.black fontSize={14} fontWeight={400} color={theme.text2}>
                      {t("Buyer's liquidated damage")}
                    </TYPE.black>
                  </RowFixed>
                  <TYPE.black fontSize={14} color={theme.text1}>
                    {ethers.utils.formatEther(props.pair.buyerLiquidataedDamages)} HYK
                  </TYPE.black>
                </RowBetween>
              </FixedHeightRow>

              <FixedHeightRow>
                <RowBetween>
                  <RowFixed>
                    <TYPE.black fontSize={14} fontWeight={400} color={theme.text2}>
                      {t("Court")}
                    </TYPE.black>
                  </RowFixed>

                  <TYPE.black fontSize={14} color={theme.text1}>

                    {getJudge(props.pair.arbitration) ? <A href={getJudge(props.pair.arbitration)?.URL} target="_blank">{getJudge(props.pair.arbitration) ? (getJudge(props.pair.arbitration)?.name) : t("Unverified Court")}</A> : <></>}
                  </TYPE.black>
                </RowBetween>
              </FixedHeightRow>

              <FixedHeightRow>
                <RowBetween>
                  <RowFixed>
                    <TYPE.black fontSize={14} fontWeight={400} color={theme.text2}>
                      {t('Describe')}:
                    </TYPE.black>
                  </RowFixed>
                </RowBetween>
              </FixedHeightRow>
              <HoverCard border={border}>
                {HandleDescribe() ? HandleDescribe() : <></>}
              </HoverCard>
              <FixedHeightRow>
                <RowBetween>
                  <RowFixed>
                    <TYPE.black fontSize={14} fontWeight={400} color={theme.text2}>
                      {t('Buyer contact info')}
                    </TYPE.black>
                    <QuestionHelper text={t("Your contact info")} />
                  </RowFixed>
                  <input onChange={(e) => { setcontactInfo(e.target.value) }}>
                  </input>
                </RowBetween>
              </FixedHeightRow>

              {handleLockButton()}

            </AutoColumn>
          )}
        </AutoColumn>
      </HoverCard>
    </>
  )
}
